.FirstTop{
  background-image:url('./wall.jpg');
  display: flex;
  align-items:center;
  flex-flow: column;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}

.RightSection-Mobile{
  display:flex;
  width:100%;
  flex:2;
  height:300px;
  overflow: hidden;
}

@media(max-width:1300px){
  .FirstTop{
    display: flex;
    flex-flow: column;
    align-items: center;
  }
}

.LeftSection p{
  color: white;
}

.LeftSection{
   height: 40%;
   display: flex;
   flex-flow:column;
   align-items: center;
   padding-top:50px;
   color: white;
   width:100%;
}

.RightSection{
    display:flex;
    flex-flow: column;
    align-items: center;
    margin-bottom:10px;
    justify-content: flex-start;
    overflow: hidden;
    height: 90%;
    width:50%;
}

.buy{
    font-size:35px;
    font-weight: bold;
}

