 }

 .RightSection-Mobile::-webkit-scrollbar{
  width:1px; 
}

.RightSection-Mobile::-webkit-scrollbar {
  display: none;
}

.RightSection-Mobile::-webkit-scrollbar-track{
  border-radius: 5px;
  box-shadow: inset 0 0 10px rgba(0, 0, 0,0.25);
}

.RightSection-Mobile::-webkit-scrollbar-thumb{
  border-radius: 5px;
  background-color: rgb(200, 149, 175);
}


 .LeftSection p{
    color: white;
    font-size:20px;
  }

  .RightSection{
   visibility:visible;
  }


  .RightSection-Mobile{
    display:flex;
    width:100%;
    flex:2;
    height:300px;
    overflow: scroll;
    /* Hide scrollbar for IE, Edge add Firefox */
   -ms-overflow-style: none;
   scrollbar-width: none; /* Firefox */
  }

  .RightSection-Mobile:hover {
    overflow-x: scroll;
  }
  
  

  .card-mobile{
    flex-shrink:0;
    width: 300px;
    height: 250px;
    border-radius:10px;
